import {Component} from 'react'
import {Button, Col, Container, Row} from "reactstrap";


export default class Examples extends Component{

    constructor(props) {
        super(props);
    }



    render() {
        return (
            <section className={'section'} id={'examples'}>
                <Container>
                    <Row className="justify-content-center">
                        <Col lg="8" md="12" className={'text-center'}>
                            <h2 className="title text-white">Example Projects</h2>
                            <h4 className="description">
                                Take a look at some projects I have been involved in.
                            </h4>
                        </Col>
                    </Row>
                </Container>
                <Container fluid>
                    <div className={'blur-hover'}>
                            <a href={'/examples'}>
                                <img
                                    alt={'See examples'}
                                    className={'blur-item examples-img'}
                                    src={require("assets/img/technology.jpg").default}
                                />
                                <span className="blur-hidden h2 text-black"><strong>Explore examples</strong></span>
                            </a>
                    </div>

                </Container>

            </section>
        )
    }

}